"use client"

import React, { useState } from "react";
import { ArrowUpRight, ArrowDownRight, Users, BedDouble, DollarSign, CalendarCheck, Plus, ChefHat, CreditCard, CheckCircle2 } from "lucide-react";
import { BookingModal } from "./BookingModal";
import { SettlementModal } from "./SettlementModal";
import { Button } from "@/components/ui/button";

const stats = [
    {
        name: "Total Revenue",
        value: "$45,231.89",
        change: "+20.1%",
        trend: "up",
        icon: DollarSign,
    },
    {
        name: "Occupancy Rate",
        value: "84%",
        change: "+4.5%",
        trend: "up",
        icon: BedDouble,
    },
    {
        name: "Active Guests",
        value: "128",
        change: "-1.2%",
        trend: "down",
        icon: Users,
    },
    {
        name: "Pending Bookings",
        value: "12",
        change: "+8.2%",
        trend: "up",
        icon: CalendarCheck,
    },
];

const recentBookings = [
    {
        id: "B-1023",
        guest: "Alice Johnson",
        room: "Deluxe Suite (304)",
        dates: "Jan 30 - Feb 4",
        status: "Confirmed",
        amount: "$1,240",
    },
    {
        id: "B-1022",
        guest: "Robert Smith",
        room: "Standard Double (201)",
        dates: "Jan 31 - Feb 2",
        status: "Checked In",
        amount: "$450",
    },
    {
        id: "B-1021",
        guest: "Emily Davis",
        room: "Ocean View (502)",
        dates: "Feb 5 - Feb 10",
        status: "Pending",
        amount: "$2,100",
    },
    {
        id: "B-1020",
        guest: "Michael Brown",
        room: "Standard Single (104)",
        dates: "Jan 29 - Jan 30",
        status: "Checked Out",
        amount: "$180",
    },
];

export default function DashboardPage() {
    const [isBookingModalOpen, setIsBookingModalOpen] = useState(false);

    // Settlement State
    const [unpaidOrders, setUnpaidOrders] = useState<any[]>([]);
    const [isSettlementModalOpen, setIsSettlementModalOpen] = useState(false);
    const [selectedOrder, setSelectedOrder] = useState<any>(null);

    React.useEffect(() => {
        const loadOrders = () => {
            const allOrders = JSON.parse(localStorage.getItem('restaurant_orders') || '[]');
            const pending = allOrders.filter((o: any) => o.status === "Unpaid");
            setUnpaidOrders(pending);
        };
        loadOrders();
        window.addEventListener('storage', loadOrders);
        return () => window.removeEventListener('storage', loadOrders);
    }, []);

    const handleOpenSettlement = (order: any) => {
        setSelectedOrder(order);
        setIsSettlementModalOpen(true);
    };

    const handleSettle = (orderId: string, method: string) => {
        const allOrders = JSON.parse(localStorage.getItem('restaurant_orders') || '[]');
        const updatedOrders = allOrders.map((o: any) => {
            if (o.id === orderId) {
                return {
                    ...o,
                    status: "Paid",
                    paymentMethod: method,
                    paidAt: Date.now()
                };
            }
            return o;
        });
        localStorage.setItem('restaurant_orders', JSON.stringify(updatedOrders));

        // Refresh local state
        setUnpaidOrders(updatedOrders.filter((o: any) => o.status === "Unpaid"));

        // Trigger storage event for other tabs (like POS)
        window.dispatchEvent(new Event("storage"));
    };

    return (
        <div className="space-y-8">
            <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
                <div>
                    <h1 className="text-3xl font-serif text-gray-900 dark:text-white">Dashboard</h1>
                    <p className="text-gray-500 dark:text-gray-400">Overview of your hotel's performance today.</p>
                </div>
                <Button onClick={() => setIsBookingModalOpen(true)} className="gap-2">
                    <Plus className="h-4 w-4" />
                    New Booking
                </Button>
            </div>

            {/* KPI Grid */}
            <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-4">
                {stats.map((stat) => (
                    <div
                        key={stat.name}
                        className="rounded-xl border border-gray-200 dark:border-gray-800 bg-white dark:bg-black p-6 shadow-sm"
                    >
                        <div className="flex items-center justify-between">
                            <div className="rounded-lg bg-gray-50 dark:bg-white/5 p-3">
                                <stat.icon className="h-6 w-6 text-gray-700 dark:text-white" />
                            </div>
                            <span
                                className={`flex items-center gap-1 text-xs font-medium ${stat.trend === "up" ? "text-green-600" : "text-red-600"
                                    }`}
                            >
                                {stat.change}
                                {stat.trend === "up" ? (
                                    <ArrowUpRight className="h-3 w-3" />
                                ) : (
                                    <ArrowDownRight className="h-3 w-3" />
                                )}
                            </span>
                        </div>
                        <div className="mt-4">
                            <p className="text-sm font-medium text-gray-500 dark:text-gray-400">{stat.name}</p>
                            <h3 className="text-2xl font-bold text-gray-900 dark:text-white mt-1">{stat.value}</h3>
                        </div>
                    </div>
                ))}
            </div>

            {/* Pending Settlements - High Priority - Always Visible */}
            <div className="rounded-xl border border-orange-200 bg-orange-50/50 dark:bg-orange-950/20 dark:border-orange-900/50 p-6 shadow-sm">
                <div className="flex items-center justify-between mb-4">
                    <div className="flex items-center gap-3">
                        <div className="p-2 bg-orange-100 dark:bg-orange-900/50 rounded-lg text-orange-600 dark:text-orange-400">
                            <ChefHat className="h-6 w-6" />
                        </div>
                        <div>
                            <h2 className="text-lg font-bold text-zinc-900 dark:text-zinc-100">Restaurant Settlements</h2>
                            <p className="text-sm text-zinc-500">Pending payments from POS</p>
                        </div>
                    </div>
                    <span className="bg-orange-200 dark:bg-orange-900 text-orange-800 dark:text-orange-200 px-3 py-1 rounded-full text-xs font-bold">
                        {unpaidOrders.length} Pending
                    </span>
                </div>

                {unpaidOrders.length === 0 ? (
                    <div className="flex flex-col items-center justify-center p-8 text-zinc-500 gap-2 border border-dashed border-zinc-200 dark:border-zinc-800 rounded-lg">
                        <CheckCircle2 className="h-8 w-8 text-green-500 opacity-50" />
                        <p className="text-sm font-medium">No pending settlements</p>
                        <p className="text-xs">All tables are cleared and paid.</p>
                    </div>
                ) : (
                    <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
                        {unpaidOrders.map((order) => (
                            <div
                                key={order.id}
                                onClick={() => handleOpenSettlement(order)}
                                className="bg-white dark:bg-zinc-900 rounded-lg p-4 border border-zinc-200 dark:border-zinc-800 hover:border-orange-500 cursor-pointer shadow-sm transition-all group"
                            >
                                <div className="flex justify-between items-start mb-2">
                                    <span className="font-mono text-xs text-zinc-500">{order.id}</span>
                                    <span className="font-bold text-lg">${order.total.toFixed(2)}</span>
                                </div>
                                <div className="space-y-1">
                                    <div className="flex items-center gap-2 text-sm font-medium">
                                        <span className="bg-zinc-100 dark:bg-zinc-800 px-1.5 py-0.5 rounded text-zinc-600 dark:text-zinc-400 text-xs">Table {order.table}</span>
                                        <span className="truncate">{order.customer}</span>
                                    </div>
                                    <p className="text-xs text-zinc-400 flex items-center gap-1">
                                        <CalendarCheck className="h-3 w-3" />
                                        {order.time}
                                    </p>
                                </div>
                                <Button size="sm" className="w-full mt-3 bg-white dark:bg-zinc-950 border border-zinc-200 dark:border-zinc-800 text-zinc-900 dark:text-zinc-100 hover:bg-orange-50 dark:hover:bg-orange-900/20 hover:border-orange-200 group-hover:border-orange-500 transition-colors">
                                    Settle Bill
                                </Button>
                            </div>
                        ))}
                    </div>
                )}
            </div>

            {/* Recent Bookings & Chart Placeholder */}
            <div className="grid gap-6 lg:grid-cols-3">
                {/* Recent Bookings Table */}
                <div className="lg:col-span-2 rounded-xl border border-gray-200 dark:border-gray-800 bg-white dark:bg-black shadow-sm overflow-hidden">
                    <div className="flex items-center justify-between p-6 border-b border-gray-100 dark:border-gray-900">
                        <h2 className="text-lg font-semibold font-serif text-gray-900 dark:text-white">Recent Bookings</h2>
                        <button className="text-sm font-medium text-blue-600 hover:text-blue-500 dark:text-blue-400">View All</button>
                    </div>
                    <div className="overflow-x-auto">
                        <table className="w-full text-left text-sm text-gray-500 dark:text-gray-400">
                            <thead className="bg-gray-50 dark:bg-white/5 text-xs uppercase text-gray-700 dark:text-gray-300">
                                <tr>
                                    <th className="px-6 py-4 font-medium">Guest</th>
                                    <th className="px-6 py-4 font-medium">Room</th>
                                    <th className="px-6 py-4 font-medium">Dates</th>
                                    <th className="px-6 py-4 font-medium">Status</th>
                                    <th className="px-6 py-4 font-medium text-right">Amount</th>
                                </tr>
                            </thead>
                            <tbody className="divide-y divide-gray-100 dark:divide-gray-800">
                                {recentBookings.map((booking) => (
                                    <tr key={booking.id} className="hover:bg-gray-50 dark:hover:bg-white/5 transition-colors">
                                        <td className="px-6 py-4 font-medium text-gray-900 dark:text-white">{booking.guest} <br /><span className="text-xs font-normal text-gray-400">{booking.id}</span></td>
                                        <td className="px-6 py-4">{booking.room}</td>
                                        <td className="px-6 py-4">{booking.dates}</td>
                                        <td className="px-6 py-4">
                                            <span
                                                className={`inline-flex items-center rounded-full px-2.5 py-0.5 text-xs font-medium ${booking.status === "Confirmed"
                                                    ? "bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-400"
                                                    : booking.status === "Checked In"
                                                        ? "bg-blue-100 text-blue-800 dark:bg-blue-900/30 dark:text-blue-400"
                                                        : booking.status === "Pending"
                                                            ? "bg-yellow-100 text-yellow-800 dark:bg-yellow-900/30 dark:text-yellow-400"
                                                            : "bg-gray-100 text-gray-800 dark:bg-gray-800 dark:text-gray-400"
                                                    }`}
                                            >
                                                {booking.status}
                                            </span>
                                        </td>
                                        <td className="px-6 py-4 text-right font-medium text-gray-900 dark:text-white">{booking.amount}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>

                {/* Room Status Widget */}
                <div className="rounded-xl border border-gray-200 dark:border-gray-800 bg-white dark:bg-black p-6 shadow-sm">
                    <h2 className="text-lg font-semibold font-serif text-gray-900 dark:text-white mb-6">Room Status</h2>
                    <div className="space-y-6">
                        {[
                            { label: "Booked", count: 45, color: "bg-blue-500", percentage: "75%" },
                            { label: "Available", count: 12, color: "bg-green-500", percentage: "20%" },
                            { label: "Maintenance", count: 3, color: "bg-red-500", percentage: "5%" },
                        ].map((item) => (
                            <div key={item.label}>
                                <div className="flex justify-between items-center mb-2">
                                    <span className="text-sm font-medium text-gray-700 dark:text-gray-300">{item.label}</span>
                                    <span className="text-sm text-gray-500">{item.count} rooms</span>
                                </div>
                                <div className="w-full bg-gray-100 dark:bg-gray-800 rounded-full h-2.5">
                                    <div className={`h-2.5 rounded-full ${item.color}`} style={{ width: item.percentage }}></div>
                                </div>
                            </div>
                        ))}
                    </div>

                    <div className="mt-8 rounded-lg bg-gray-50 dark:bg-white/5 p-4 border border-dashed border-gray-200 dark:border-gray-800">
                        <div className="text-center">
                            <p className="text-sm font-medium text-gray-900 dark:text-white">Need a quick clean?</p>
                            <p className="text-xs text-gray-500 mt-1">3 rooms are marked dirty.</p>
                            <button className="mt-3 w-full rounded-md bg-white border border-gray-200 px-3 py-2 text-xs font-semibold shadow-sm hover:bg-gray-50 dark:bg-black dark:border-gray-700 dark:text-white dark:hover:bg-gray-900">
                                Notify Housekeeping
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            <BookingModal open={isBookingModalOpen} onOpenChange={setIsBookingModalOpen} />
            <SettlementModal
                open={isSettlementModalOpen}
                onOpenChange={setIsSettlementModalOpen}
                order={selectedOrder}
                onSettle={handleSettle}
            />
        </div>
    );
}
